* {
  padding: 0;
  margin: 0;
}
.fixed {
  position: sticky;
  top: 0;
  z-index: 100;
}
.header_container {
  background-color: #036444;
  display: flex;
  gap: 10px;
  align-items: center;
  color: white;
  height: 70px;
}

.logo_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  min-width: 180px; /* smaller like Netflix */
  gap: 5px;
}

.logo_container img {
  height: 35px; /* FIX: perfect header size */
  width: auto; /* keeps original proportions */
  padding: 0; /* remove padding */
  margin-left: 10px; /* small spacing only (optional) */
  margin-top: 20px;
}
.delivery {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
  font-weight: bold;
  padding: 5px;
}
.delivery p {
  font-size: 10px;
}
.search {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  flex: 1;
}

.search select,
.search input {
  padding: 10px;
  font-weight: bold;
  outline: 1px solid white;
}
.search select {
  border: none;
}
.search input {
  border: 1px solid white;
  width: 100%;
  margin-right: -2px;
}
.search svg {
  padding: 7px;
  background: #febd69;
  color: #1a1a1a;
}
.search svg:hover {
  padding: 7px;
  background-color: #d49644;
}
.search:focus-within {
  border: 2px solid rgb(235, 161, 173);
}
.order_container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 450px;
  gap: 10px;
}
.order_container a {
  text-decoration: none;
  color: white;
}
.order_container p {
  font-size: 10px;
}
.language {
  width: 100px;
  display: flex;
  padding: 10px;
}
.language select {
  background-color: #1a1a1a;
  border: none;
  color: white;
}
.language img {
  width: 40%;
}

.cart {
  position: relative;
}
.cart span {
  position: absolute;
  font-weight: bold;
  top: -5px;
  font-size: 15px;
  /* background-color: #1a1a1a; */
  left: 13px;
  color: white;
}
.order_container a:hover,
.delivery:hover,
.logo_container a:hover,
.lower_container ul li:hover {
  border: 1x solid white;
  border-radius: 3px;
}
.lower_container {
  background-color: #085749;
  color: white;
}
.lower_container ul {
  display: flex;
  list-style: none;
  gap: 5px;
}
.lower_container ul li {
  padding: 8px;
}
.lower_container ul li:first-child {
  display: flex;
  gap: 2px;
  align-items: center;
}

@media screen and (max-width: 576px) {
  .header_container {
    display: flex;
    flex-direction: column !important;
    height: auto;
  }

  .lower_container ul li:not(:first-child) {
    display: none;
  }
  .language {
    display: none;
  }
}
